.settings {
   display: flex;
    display: -webkit-flex;
    justify-content: space-around;
    margin-top: 5px;
  }
  
  .settings-section {
    text-align: center;
  }
  
.settings-section label{
  font-size: 1rem;
}

 .settings-section button {
    font-size: 1.2rem;
    border: none;  /*1px solid #534C98;*/
    background-color: rgba(255, 255, 255, 0.1);
    color: #FDF6F0;
    border-radius: 5px;
    outline: none;
    width: 30px;
    height: 30px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    transition: all 0.25s ease-out;
  }
  
  .settings-section button:not(.disable):hover {
    cursor: pointer;
    background-color: #388E3C;
  }
  
  .settings-section button.disable {
    opacity: 0.5;
    cursor: not-allowed;
  }
  
  .settings-section span {
    width: 35px;
    height: 35px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    //background-color: #FDF6F0;
    border-radius: 5px;
    color: #FDF6F0;
    margin-top: 10px;
    margin-left: 10px;
    margin-right: 10px;
    font-size: 1.5rem;
    font-weight: 600;
  }


  @media screen and (max-width: 600px) {
    .settings-section label{
      font-size: 0.7rem;
    }
    .settings-section button {
      font-size: 0.7rem;
    }
    .settings-section span {
      font-size: 1.3rem;
    }
  }
.times {
    width: 100%;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
  }
  
  .times-content {
    display: inline-block;
    width: 280px;
    height: 120px;
    margin-top: 45px;
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: 'Varela Round', sans-serif;
  }
  
  #timer-label {
    font-size: 1.5rem;
    font-family: 'Montserrat', sans-serif; 
  }
  
  #time-left {
    font-size: 6rem;
    font-weight: 700;
  }

  @media screen and (max-width: 600px) {
    #timer-label {
      font-size: 1.3rem;
    }
    #time-left {
      font-size: 2.4rem;
      font-weight: 700;
    }
    .times-content {
      margin-top: 10px;
      height: 100px;
    }
  }
.controller {
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    margin-top: 15px;
  }
  
  .controller button {
    font-size: 1.1rem;
    font-weight: 700;
    border: none; /* 1px solid #534C98;*/
    background-color: rgba(255, 255, 255, 0.1);
    color: #FDF6F0; /*#FDF6F0;*/
    border-radius: 5px;
    outline: none;
    width: 85px;
    height: 35px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    transition: all 0.25s ease-out;
    margin: 5px;
    text-transform: uppercase;
  }
  
  .controller button:hover {
    cursor: pointer;
    background-color: #388E3C;
  }


  @media screen and (max-width: 600px) {
    .controller button {
      font-size: 1rem;
    }
    .controller {
      margin-top: 0px;
    }
  }
