  margin-bottom: 40px;
}
.author {
font-size: 15px;
}
.timer-icon {
  -webkit-animation: wiggle 15s infinite;
          animation: wiggle 15s infinite;
}
@-webkit-keyframes wiggle{
  0%{-webkit-transform: rotate(-0deg);transform: rotate(-0deg)}
  2%{-webkit-transform: rotate(-30deg);transform: rotate(-30deg)}
  7%{-webkit-transform: rotate(30deg);transform: rotate(30deg)}
  12%{-webkit-transform: rotate(0deg);transform: rotate(0deg)}
  100%{-webkit-transform: rotate(0deg);transform: rotate(0deg)}
}
@keyframes wiggle{
  0%{-webkit-transform: rotate(-0deg);transform: rotate(-0deg)}
  2%{-webkit-transform: rotate(-30deg);transform: rotate(-30deg)}
  7%{-webkit-transform: rotate(30deg);transform: rotate(30deg)}
  12%{-webkit-transform: rotate(0deg);transform: rotate(0deg)}
  100%{-webkit-transform: rotate(0deg);transform: rotate(0deg)}
}





